import React from "react";
import "./AParcelSearch.css";
import { Form, Row, Col, Input, Button,Select,} from 'antd';
 


 //const ReturnInfo = JSON.parse(JSON.stringify(postInfo)) obj

export default function AParcelSearch(props){ 


  const [form] = Form.useForm(); 
  const { Option } = Select;

    
  const onFinish = values => {
    props.setSearch({userId:values.userId,
                    orderId:values.orderId,
                    username:values.username,
                    searchRg:values.searchRange,
                    searchTy:values.searchType}) 
                    };

    return (
      <div>
        <p className="ParcelSearchIntro"> <span>;   </span></p>
      <Form
        form={form}
        name="SearchOrder"
        className="Aparcel-search-form"
        onFinish={onFinish}
      >
        <Row gutter={24}>
          <Col span={8} key={1}> 
              <Form.Item
              name="userId"
              label="ID"
              > 
            <Input />  
          </Form.Item>
          </Col>

          <Col span={8} key={2}> 
              <Form.Item
              name="username"
              label=""
              > 
            <Input />  
          </Form.Item>
          </Col>

          <Col span={8} key={3}> 
                <Form.Item
                name="orderId"
                label="ID"
                > 
              <Input />  
            </Form.Item>
            </Col>
        </Row>

        <Row>
          <Col span={12} key={4}> 
            <Form.Item
                className="searchAll"
                name="searchRange"
                label=""
                > 
              <Select className="select">
                <Option value=""></Option>
                <Option value="searchAll"></Option>
              </Select>
            </Form.Item>
          </Col>
          <Col span={12} key={5}> 
            <Form.Item
              className="searchStatus"
              name="searchType"
              label=""
              > 
              <Select className="select" >
                <Option value=""></Option>
                <Option value="waiting"></Option>
                <Option value="problem"></Option>
                <Option value="verify"></Option>
              </Select>
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <Col
            span={24}
            key={6}
           // push={10}
            style={{
              textAlign: 'center',
              justifyContent:'center'
            }}
          >
            <Button type="primary" htmlType="submit" >   
              
            </Button>
            <Button
              style={{
                margin: '0 8px',
              }}
              onClick={() => {
                //form.resetFields();
                props.setSearch({userId:"",
                    orderId:"",
                    username:"",
                    searchRg:"",
                    searchTy:""}) 
              }}
            >
              
            </Button>
            
            <Button
              style={{
                margin: '0 8px',
              }}
              onClick={() => {
                form.resetFields();
              }}
            >
              
            </Button>
          </Col>
        </Row>
      </Form>
      </div>
    )}