
import React,{useState, useEffect}from "react";
import { Link } from 'react-router-dom'; 
import {AutoComplete,Form,notification,Input,Button} from 'antd';
import _get from "lodash/get";
import "./AParcelUpdate.css";

export default function AParcelUpdate(props) {

  const FormItem = Form.Item;

  const [form] = Form.useForm();

  const thisParcelId = props.match.params.id;


    /*---------------------parcelID parcel ------------------------- */

  const [dataSource,setDataSource] = useState()  


  useEffect(()=>{
    fetch(`/api/parcel/find/${thisParcelId}`, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // dictionary Json [{"key":"123"}] obj 
    .then(data => {
      setDataSource(data);  //[{}{}{}{}] datalist
      console.log("")
      console.log(`/api/parcel/find/${thisParcelId}`)
      console.log(data)
     })
    
  },[]) 

  useEffect(()=>{form.setFieldsValue(
                    { 
                      DName:DefaultName,DAddress: DefaultAddress,DPostCode:DefaultPostCode, DPhone: DefaultPhone,
                      DContentType: DefaultContentType, DDescription: DefaultDescription,DComment: DefaultComment,
                    }
                  )
                }
              )

 // useEffect(()=>{setDisabled(InitialState())},[]) //dataSource.statusId   

/***   ** */
  const openNotification = (resp) => {
      notification['success']({
        message: 'Successfully update user information',
        description:
          `${resp.comment} You have successfully unpdate your personnal information`,
        })
    };

/**---------  pathInfo -----------*** */
  const renderTitle = title => (
    <span>
      {title}
    </span>
  );

  const renderItem = (title) => ({
    value: title,
    label: (
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        {title}
      </div>
    ),
  });

  const Type =["1 ","2 ","3 e","4 EMS","5 "] 

  const options_type = [
  {
  label: renderTitle(''),
  options: Type.map((item,index)=>renderItem(item))
  }]
  

/**********   input Defaultvalue ********/


// 

  const DefaultName = _get(dataSource,"senderName");
  const DefaultAddress= _get(dataSource,"senderAddress");
  const DefaultPostCode = _get(dataSource,"senderPostCode");
  const DefaultPhone = _get(dataSource,"senderPhone");
  const DefaultDescription = _get(dataSource,"description");
  const DefaultComment = _get(dataSource,"comment");
  const DefaultContentType = _get(dataSource,"contentType");
 


/********** *         **********/
  const handleSubmit = (values)=>{
    
    const newParcelInfo =
      {
        id:thisParcelId,
        orderNumber: values.DOrderNumber , //thisOrderData.price,    
        
        senderName: values.DName,
        senderAddress: values.DAddress,
        senderPhone: values.DPhone,
        senderPostCode: values.DPostCode,

        userId: dataSource.userId,

        receiverName: dataSource.receiverName,  // parcel
        receiverPhone: dataSource.receiverPhone,
        receiverAddress: dataSource.receiverAddress,
        receiverPostCode: dataSource.receiverPostCode,

        contentType: values.DContentType,

        description: values.DDescription,
        
        orderId: dataSource.orderId,
       
        parcelStatus: dataSource.parcelStatus,

       
        userOrderId: dataSource.userOrderId,

        comment: values.DComment,   // Admincomment   
      }
    
      console.log("")
      console.log(newParcelInfo)

    fetch("/api/parcel/update", { 
      method: "PUT",
      headers: {
      "Content-Type": "application/json"
      },
      body: JSON.stringify(newParcelInfo),
      })
      .then(resp => resp.json())
      .then(resp=>{
        console.log("")
        console.log(resp)
        openNotification(resp)})
     
  }
 
  return (  //   
   <div className="UpdateOrderForm">
        <div><p> {thisParcelId} </p></div>
     <Form 
     form={form}
     onFinish={handleSubmit}
     className="parcelUpdate-form"
     
     >
        <div ><p className ="LabelStyle"></p></div>
        <FormItem  name ="DName" >
          <Input/>
        </FormItem>
        <div ><p className ="LabelStyle"></p></div>
        <FormItem  name ="DAddress">
          <Input/>
        </FormItem>

        <div ><p className ="LabelStyle"></p></div>
        <FormItem name = "DPostCode">
          <Input/>
        </FormItem>

        <div ><p className ="LabelStyle"></p></div>
        <FormItem name= "DPhone">
          <Input/>
        </FormItem>

        <div ><p className ="LabelStyle"></p></div>
        <FormItem name= "DDescription">
        <Input/> 
        </FormItem>  

        <div ><p className ="LabelStyle"></p></div>
        <FormItem name= "DComment">
        <Input/> 
        </FormItem>       

        <div ><p className ="LabelStyle"></p></div>
        <FormItem name= "DContentType">
          <AutoComplete
            dropdownClassName="certain-category-search-dropdown"
            dropdownMatchSelectWidth={300}
            style={{
              width: 300,
            }}
          options={options_type}
          >
            <Input.Search />   
          </AutoComplete>
        </FormItem>



        <FormItem>
          <Button type="primary" htmlType="submit" >  
            
          </Button> 

          <Link to="/Admin/Parcel">
          <Button type="primary">  
            
          </Button> 
          </Link>
    
      </FormItem>

     </Form>
   </div> 
  );

}