import React,{useState,useEffect} from 'react';
import {Table,Tag,notification,Rate,Modal } from "antd";
import {EditOutlined,ThunderboltFilled,HeartFilled} from '@ant-design/icons'; // <ThunderboltFilled />
import { Link } from "react-router-dom";
import { Drawer, Form, Button, Col, Row, Input, Select, DatePicker } from 'antd';
import "./NoCommit.css";


export default function NoCommit(props) {

  // record 
    const userId = props.userId // props.user.obj.uid.toString();
    const userName = props.userName //props.user.obj.username.toString();

  // 

    const FormItem = Form.Item;

    const [form] = Form.useForm();

    const [drawer,setDrawer] = useState({ visible: false, drawerOId:"",drawerUId:userId}) 

    const  showDrawer = (prop) => {
        setDrawer({
        visible: true,
        drawerOId: prop, // prop,  // record.id 
        drawerUId:userId,// userId  
        });
    };

    const onClose = () => {
        setDrawer({
        visible: false,
        drawerOId:"",
        drawerUId:userId,
        });
    };

    const handleSubmit = (values)=>{

      const SumScore = values.star + values.heart

      const CreateComment =
      {
        userId: drawer.drawerUId,
        userOrderId:drawer.drawerOId,
        score: SumScore,
        comment:values.description,
      }
    

    fetch("/api/orderhistory/create", { 
      method: "POST",
      headers: {
      "Content-Type": "application/json"
      },
      body: JSON.stringify(CreateComment),
      })
      .then(resp => resp.json())
      .then(showModal())  // 
    }

// 

const [modalState,setModalState] = useState({visible: false}) 

const showModal = () => {
  setModalState({
    visible: true,
  });
};

const handleOk = e => {
  //console.log(e);
  setModalState({
    visible: false,
  });
  // 
  setDrawer({
    visible: false,
    drawerOId:"",
    drawerUId:userId,
    })
};

const handleCancel = e => {
  //console.log(e);
  setModalState({
    visible: false,
  });
};


const columns = (showDrawer)=>{
  return [
  {
    title: "",
    dataIndex: "id",
    key: "id",
  },
  {
    title: '',
    key: 'statusId',
    dataIndex: 'statusId',
    },
  {
    title: "",  //   record.id 
    dataIndex: "action",
    key: "action",
    render: (text, record, index) => { 
      return (
        <div>
            <Link to={`/Business/History/Commit/${record.id}`} className="More"> 
            <EditOutlined/>
            </Link>
        </div>
      );}
  },
  {
    title: "",  //   record.id 
    dataIndex: "action",
    key: "action",
    render: (text, record, index) => { 
      return (
        <Button type="primary" onClick={()=>showDrawer(record.id)}>  
            
        </Button>
      );}
  },
];}
//showDrawer(record.id)    /// onclick  record id //https://blog.csdn.net/hl_qianduan/article/details/89372881?utm_medium=distribute.pc_relevant.none-task-blog-BlogCommendFromMachineLearnPai2-2.channel_param&depth_1-utm_source=distribute.pc_relevant.none-task-blog-BlogCommendFromMachineLearnPai2-2.channel_param

let tableDataSource = [];

if(props.recordNeedCmt){
  tableDataSource = props.recordNeedCmt.map(item => {
    return {
      ...item,
      key: item.id
    }
  })
}

return(

  <div>
  <p></p>
  <br/>
  <Table 
  dataSource={tableDataSource}
  columns={columns(showDrawer)}   // 
  expandable={
    {
      expandedRowRender: (record) => {
        return(
        <div>
          <p> &nbsp; {record.receiverName} &nbsp;   &nbsp; {record.receiverAddress } &nbsp; {record.receiverPostCode} </p>
          <p>  &nbsp; {record.weight } &nbsp;   &nbsp; {record.volumn}</p>
          <p>  &nbsp; {record.price}  &nbsp; {record.paymentInfo}</p>
        </div>
        )}
    }
  }
   />

    <Drawer
          title=""
          width={720}
          onClose={onClose}
          visible={drawer.visible}
          bodyStyle={{ paddingBottom: 80 }}
          footer={
            <div
              style={{
                textAlign: 'right',
              }}
            >
              <Button onClick={onClose} style={{ marginRight: 8 }}>
                
              </Button>
             
            </div>
          }
    >
      <Form 
      layout="vertical" 
      hideRequiredMark
      form={form}
      onFinish={handleSubmit}>
        <Row>
            <Col span={4}>
            {userName}  {drawer.drawerUId}   {drawer.drawerOId} 
            </Col>
        </Row>
        <Row>
          <Col span={20}>
            <Form.Item
              className="heart"
              name="heart"
              label=""
              rules={[{ required: true, message: 'Please choose the star' }]}
            >
              <Rate allowClear={true} allowHalf style={{ fontSize: 36 }} character={<HeartFilled />} />
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={20}>
            <Form.Item
              className="flash"
              name="flash"
              label=""
              rules={[{ required: true, message: 'Please choose the star' }]}
            >
              <Rate allowClear={true} allowHalf style={{ fontSize: 36 }} character={<ThunderboltFilled />} />
            </Form.Item>
          </Col>
        </Row>
        
        <Row gutter={16}>
          <Col span={24}>
            <Form.Item
              name=""
              label="Description"
              rules={[
                {
                  required: true,
                  message: 'please enter url description',
                },
              ]}
            >
              <Input.TextArea rows={4} placeholder="please enter url description" />
            </Form.Item>
          </Col>
        </Row>
        <Row>
            <FormItem>
                <Button type="primary" htmlType="submit"  >  
                    
                </Button> 
            </FormItem>
        </Row>
      </Form>
    </Drawer>

    <Modal
      title=""
      visible={modalState.visible}
      onOk={handleOk}
      onCancel={handleCancel}
    >
      <p> </p>
    </Modal>
</div>

)
  }
