import React,{useState,useRef}from "react";
//import _get from "lodash/get";
import {Table, Form, Popover, Button,Tag,notification,Select,Input,Space } from "antd";
import Highlighter from 'react-highlight-words';
import {EditOutlined,StopOutlined,SearchOutlined,CloseCircleOutlined,LogoutOutlined} from '@ant-design/icons';
import { Link } from "react-router-dom";
import "./AParcelForm.css";

const FormItem = Form.Item;
const { Option } = Select;

//    API

export function ChangeParcelStatus (prop){

  const onFinish = values => {  

  //console.log("record ")
  //console.log(prop.id)  

  if(values.parcelStatus==="waiting"){
    fetch(`/api/parcel/${prop.id}/waiting`, { 
      method: "PUT",
      headers: {
      "Content-Type": "application/json"
      },
      })
      .then(resp => resp.json())
      .then(data=> prop.setRecord([data]))  //  default function AParcelFormsetRecord
      .then(openNotification()
      )

  } else if(values.parcelStatus==="verify"){
    fetch(`/api/parcel/${prop.id}/verify`, { 
      method: "PUT",
      headers: {
      "Content-Type": "application/json"
      },
      })
      .then(resp => resp.json())
      .then(data=> prop.setRecord([data]))  //  default function AParcelFormsetRecord
      .then(openNotification()
      )

  } else if(values.parcelStatus==="problem"){
    fetch(`/api/parcel/${prop.id}/problem`, { 
      method: "PUT",
      headers: {
      "Content-Type": "application/json"
      },
      })
      .then(resp => resp.json())
      .then(data=> prop.setRecord([data]))  //  default function AParcelFormsetRecord
      .then(openNotification()
      )
  }else{console.log("")}

  };
  return(
  <Form onFinish={onFinish}
  initialValues={{parcelStatus:"verify"}
  }>

    <FormItem key={1}
    name="parcelStatus"
    rules={[
      {
        required: true,
        message: 'Please input parcel delivered number!',
      },
    ]}>
          <Select style={{ width: 120 }}>
              <Option value="waiting"></Option>
              <Option value="verify">/</Option>
              <Option value="problem"></Option>
          </Select>  
    </FormItem>

    <FormItem key={2}>
      <Button type="primary" htmlType="submit">  
         
      </Button> 
    </FormItem>
  </Form>
);}

//   ------------------

export function AddParcelToOrder(prop){

  const onFinish = values => {
  fetch(`/api/parcel/move?newOrderId=${values.orderID}&orginOrderId=-1`, { 
    method: "PUT",
    headers: {
    "Content-Type": "application/json"
    },
    body: JSON.stringify([prop.id]),
    })
    .then(resp => resp.json())
    .then(data=> prop.setRecord(data))  // parcel  [{}] [data]
    .then(openNotification()) //    clear  
  };
  return(
  <Form onFinish={onFinish}
  initialValues={{
  }}>
      <FormItem key={1}
      name="orderID"
      rules={[
          {
          required: true,
          message: '',
          },
      ]}>
          <Input placeholder="" />  
      </FormItem>
      <FormItem key={2}>
          <Button type="primary" htmlType="submit">  
          
          </Button> 
      </FormItem>
      </Form>
);}

/*--*/
const openNotification = () => {
  notification['success']({
    message: '',
    description:
      '' 
    })
};


export default function AParcelForm(props) {

//const userId = props.user.obj.uid;
 
  /*--*/

  const [searchText, setSearchText] = useState("");
  const [searchedColumn, setSearchedColumn] = useState("");
  var searchInput = useRef(null); 

  const getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={node => {
            searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            Search
          </Button>
          <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
            Reset
          </Button>
        </Space>
      </div>
    ),
    filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
    onFilter: (value, record) =>
      record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()),
    onFilterDropdownVisibleChange: visible => {
      if (visible) {
        setTimeout(() => searchInput.select());   
      }
    },
    render: text =>
      searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[searchText]}
          autoEscape
          textToHighlight={text.toString()}
        />
      ) : (
        text
      ),
  });
  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0])
    setSearchedColumn(dataIndex)
  };
  const handleReset = clearFilters => {
    clearFilters();
    setSearchText("")
  };
    
  /*  */

    // 
    const deleteParcel = (record)=>{
   fetch(`/api/parcel/delete/parcel/${record.id}/userorder/${record.userOrderId}`, { 
    method: "DELETE",
    headers: {
    "Content-Type": "application/json"
    },
    }) 
    .then(props.setRecord([]))      // record record use Effect record.length  
    .then(openNotification())    //    "1"
  }
    // 
    const moveoutParcel = (record)=>{
  
    fetch(`/api/parcel/moveout/parcel/${record.id}/userorder/${record.userOrderId}`, { 
     method: "PUT",
     headers: {
     "Content-Type": "application/json"
     },
     })
     .then(resp => resp.json())
     .then(resp=>props.setRecord([resp]))  
     .then(openNotification())     // record record use Effect record.length  
   }

  
  //------------------------columns --------------------

  const columns = (deleteParcel ,moveoutParcel)=>{
    return [
        {
            title: "ID",
            dataIndex: "userId",
            key: "userId",
            render: text => <div>{text}</div>, // value  
            },
        {
          title: "ID",
          dataIndex: "id",
          key: "id",
          ...getColumnSearchProps('id'),
        },   
        {
          title: '',
          key: 'parcelStatus',
          dataIndex: 'parcelStatus',
          filters: [
            {
              text: '',
              value: 'waiting',
            },
            {
              text: '',
              value: 'verify',
            },
            {
              text: '',
              value: 'problem',
            }],
          onFilter: (value, record) => record.parcelStatus.indexOf(value) === 0,
          render: (text, record, index) => { 
                let status = ""
                let color = ""  
                if (record.parcelStatus === "problem"){
                color = "error";
                status = ""
                } else if(record.parcelStatus === "verify"){
                color = "success";
                status = "/"
                } else if(record.parcelStatus === "waiting"){
                color = 'warning';
                status = ""
                }
                return (
                <Tag color={color} key={index}>
                    {status}
                </Tag>
                );
          }},
        {
            title: "",  //   record.id 
            dataIndex: "action",
            key: "action",
            render: (text, record, index) => {  // recorddata  datasource  
                return(
                <div>
                <Popover
                    content={<ChangeParcelStatus id={record.id} setRecord={props.setRecord}/>}   
                    title={"" + record.id+ ""}  //{props.user.obj.username + " edit"} //  props.user.obj   const objectId = _get(item, "id");
                    trigger="click"
                    placement="left"
                >
                    <Button icon={<EditOutlined />} />
                </Popover>
                </div>
                )
            }
            },
            {
                title: "",
                dataIndex: "orderNumber",
                key: "orderNumber",
                ...getColumnSearchProps('orderNumber'),
                },

            {
                title: "",
                dataIndex: "userOrderId",
                key: "userOrderId",
                ...getColumnSearchProps('userOrderId'),  //  textrender
                },


            {
                title: "",  
                dataIndex: "action",
                key: "action",
                render: (text, record, index) => {  // recorddata  datasource  
                    if(record.userOrderId === -1){return (
                        <div>
                        <StopOutlined />   
                        </div>
                    );} else{return(
                        <Button shape="round" type="primary" icon ={<LogoutOutlined />} onClick={()=>moveoutParcel(record)}>     
                        </Button>
                    )}
                }
                },
            {
                title: "",  
                dataIndex: "action",
                key: "action",
                render: (text, record, index) => {  
                    return (
                        <div>
                        <Popover
                            content={<AddParcelToOrder id={record.id} setRecord={props.setRecord}/>}   
                            title={"" + record.id + ""} 
                            trigger="click"
                            placement="left"
                        >
                            <Button icon={<EditOutlined />} />
                        </Popover>
                        </div>
                    );  
                }
                },    
            {
                title: "",
                dataIndex: "action",
                key: "action",
                render: (text, record, index) => {
                return (
                     <div>
                      <Link to={`/Admin/Parcel/Update/${record.id}`} className="Commit">
                      <EditOutlined/>
                      </Link>
                    </div>  
                );
                }
            }, 
            {
                title: "", 
                dataIndex: "action",
                key: "action",
                render: (text, record, index) => {  // recorddata  datasource  
                    return(
                        <Button shape="round" type="primary" icon ={<CloseCircleOutlined />} onClick={()=>deleteParcel(record)}>     
                        </Button>
                    )
                }
            },
  ];}

  let tableDataSource;
  if(props.record){
    tableDataSource = props.record.map(item => {
      return {
        ...item,
        key: item.id
      }
    })
  }
  return (
    <div>
      <p>Hello Dear Below is your order record</p>
      <br/>
      <Table 
      dataSource={tableDataSource}
      //columns={columns}
      columns={columns(deleteParcel,moveoutParcel)}   //     
       />
      
    </div>
  );
}

