import React,{useState,useEffect}from "react";
import CreateOrder from "../CreateOrder/CreateOrder";
import OrderRecord from "../OrderRecord/OrderRecord";
import "./OrderHome.css";
 
//import { Row, col } from "antd";

export default function OrderHome(props) {

  console.log("orderHome")
  console.log(props.user.obj.uid)   // number

  const userId = props.user.obj.uid.toString();

  const [record,setRecord] = useState([])  // list table

  useEffect(()=>{
    fetch("/api/order/find/user/"+ userId, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // dictionary Json [{"key":"123"}] obj 
    .then(data => {
      setRecord(data);  //[{}{}{}{}] datalist
      console.log("value of box")
      console.log(record)
     }) 
  },[record.length])  // https://github.com/facebook/react/issues/15548 useEffect length


  record.forEach(
    function(item){
      const Pid = item.parcels.map(({id})=> "*"+id)
      item.parcelString = Pid.join('\r\n')   // ParcelString list   
      // record recordparcelString   
    }
  )
  const dataSource = record;   // datasource OrderRecord Record

  
  
  return (
    <div className="OrderHome">
    
        <p> OrderHome </p>  
        <div className="CreateOrder">
          <CreateOrder user={props.user} record={record} setRecord={setRecord}/>
        </div>
        <div>
          <OrderRecord user={props.user} record={record} setRecord={setRecord} dataSource={dataSource}/>
        </div>
        
    </div>
  ) 
}
