
import React,{useState, useEffect}from "react";
import _get from "lodash/get";
 
import "./UpdateOrderInfo.css";
import UpdateOrderForm from "../UpdateOrderForm/UpdateOrderForm";
// import OrderSubmit from "../../Test/OrderSubmit";



export default function UpdateOrderInfo(props) {

  const [dataSource,setDataSource] = useState([])  // list map undefined
  const [thisOrderData,setThisOrderData] = useState() // list table

  const userId = props.user.obj.uid;     // fetch`/api/order/findAll/${userId}` toString()  /api/order/findAll/ + userId toString()
  
  console.log('userId')
  console.log(userId)

  const thisOrderId = props.match.params.id;

  console.log(thisOrderId)


  // find/thisOrderId Order find userorderparcel order

  useEffect(()=>{
    fetch(`/api/order/find/${thisOrderId}`, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // dictionary Json [{"key":"123"}] obj 
    .then(data => {
      setThisOrderData(data);  //[{}{}{}{}] datalist
      console.log(data)
     }) 
  },[]) 

  // findAll/userId order searchuser 
  
  useEffect(()=>{
    fetch(`/api/order/findAll/${userId}`, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // dictionary Json [{"key":"123"}] obj 
    .then(data => {
      setDataSource(data);  //[{}{}{}{}] datalist
      console.log(data)
     }) 
  },[]) 

  const thisOrderStatus = _get(thisOrderData,"statusId")


  return(
    <div className="UpdateOrderInfo">
        <p> updateOrderInfo </p>  
        <div className="ChangeOrderInfo">
          <UpdateOrderForm user={props.user} thisOrderId={thisOrderId} thisOrderStatus={thisOrderStatus} thisOrderData={thisOrderData} dataSource ={dataSource}/>
        </div>
    </div>

  )
}
