import React,{useState, useEffect}from "react";
import { Transfer, Table, Tag,Button,notification } from 'antd';
import { Link } from 'react-router-dom';
import _get from "lodash/get";
import difference from 'lodash/difference';
 
import "./AddParcelToOrder.css";

// fitter 
//https://www.itranslater.com/qa/details/2136426396867101696
const openNotification = () => {
  notification['success']({
    message: 'Successfully update user information',
    description:
      'You have successfully unpdate your personnal information',
    })
  };

const TableTransfer = ({ leftColumns, rightColumns, ...restProps }) => (
  <Transfer {...restProps} showSelectAll={false}>
    {({
      direction,
      filteredItems,
      onItemSelectAll,
      onItemSelect,
      selectedKeys: listSelectedKeys,
      disabled: listDisabled,
    }) => {
      const columns = direction === 'left' ? leftColumns : rightColumns;

      const rowSelection = {
        getCheckboxProps: item => ({ disabled: listDisabled || item.disabled }),
        onSelectAll(selected, selectedRows) {
          const treeSelectedKeys = selectedRows
            .filter(item => !item.disabled)
            .map(({ key }) => key);
          const diffKeys = selected
            ? difference(treeSelectedKeys, listSelectedKeys)
            : difference(listSelectedKeys, treeSelectedKeys);
          onItemSelectAll(diffKeys, selected);
        },
        onSelect({ key }, selected) {
          onItemSelect(key, selected);
        },
        selectedRowKeys: listSelectedKeys,
      };

      return (
        <Table
          rowSelection={rowSelection}
          columns={columns}
          dataSource={filteredItems}
          size="small"
          style={{ pointerEvents: listDisabled ? 'none' : null }}
          onRow={({ key, disabled: itemDisabled }) => ({
            onClick: () => {
              if (itemDisabled || listDisabled) return;
              onItemSelect(key, !listSelectedKeys.includes(key));
            },
          })}
        />
      );
    }}
  </Transfer>
);

const leftTableColumns = [
  {
    dataIndex: 'TBindex',
    title: '', //  key  
  },
  {
    dataIndex: 'title',
    title: '',
  },
  {
    dataIndex: 'tag',
    title: '',
    render: tag => <Tag>{tag}</Tag>,
  },
  {
    dataIndex: 'description',
    title: '',
  },
  {
    dataIndex:"status",
    title:"",
    render: tag => <Tag>{tag}</Tag>,
  }
];
const rightTableColumns = [
  {
    dataIndex: 'TBindex',
    title: '',
  },
  {
    dataIndex: 'title',
    title: '',
  },
];


export default function AddParcelToOrder(props) {

  const [dataSource,setDataSource] = useState([])    //   

  const userId = props.user.obj.uid.toString();

  const thisOrderId = props.match.params.id;   // id

  useEffect(()=>{
    fetch("/api/parcel/findAll/"+ thisOrderId, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // dictionary Json [{"key":"123"}] obj 
    .then(data => {
      const originTargetKeys = data.map(x=>x.id)
      setState({ targetKeys:  originTargetKeys })
     })

      fetch("/api/parcel/findAll/userid/"+ userId, { 
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      })
      .then(resp => resp.json())   
      .then(data => {
        setDataSource(data);  
       }) 
  },[])



const mockData = dataSource.filter(
  item => item.userOrderId == thisOrderId || item.userOrderId == -1   // orderId  2     -1 
  )
  .map(
    (item,index)=>{return {
      TBindex:index,  // 
      key:item.id,   // parcel id; ID
      title: item.orderNumber,
      description: item.description,
      disabled: item.parcelStatus !== "verify", //  = 
      tag:item.contentType,
      belongToOrder:item.userOrderId,  //  order  -1
      status: item.parcelStatus,}     // verify/waiting/problem 
      }
    )
      
const [state, setState] = useState({
  targetKeys: [], //originTargetKeys,  use Effect onclickset targetkey useEffect 
  disabled: false, //    false
  showSearch: true, //  true
  })

const onChange = nextTargetKeys => { //Advice: rename this funciton. 
  setState({ targetKeys: nextTargetKeys });}   // targetKeys onChange nextTargetKeys
 

const mockList = mockData.map(item => item.key)  // key [8, 11, 12, 13, 14, 15, 16, 18]


const LeftParcelList = mockList.filter(
    function(e) {
      return this.indexOf(e) < 0;
      },
    state.targetKeys //   targetKeys  LeftParcelList 
  );


const targetParcel = mockData.filter(item=>state.targetKeys.includes(item.key))  //  

const  LeftParcel  = mockData.filter(item=>LeftParcelList.includes(item.key))  //  order-1 

const targetUpdate = targetParcel.filter(item =>item.belongToOrder == -1).map(item=>item.key)    //-12 -1 2

const LeftUpdate = LeftParcel.filter(item =>item.belongToOrder !== -1).map(item=>item.key)  //  originOrder


// onchange   

// Update  

const submitOrder =()=>{
  Promise.all(LeftUpdate.map(item=>{
        fetch(`/api/parcel/moveout/parcel/${item}/userorder/${thisOrderId}`, // 
          {  
            method: "PUT",
            headers: {
              "Content-Type": "application/json"
            },
          }); 
        }))
  .then(fetch(
    `/api/parcel/move?newOrderId=${thisOrderId}&orginOrderId=-1`,  // 
          {
            method: "PUT",
            headers: {
              "Content-Type": "application/json"
            },
            body: JSON.stringify(targetUpdate),
          }))
  .then(openNotification())
  }


    return (
      <div>
        <TableTransfer
          dataSource={mockData}
          titles={['', ''+thisOrderId+'']}
          targetKeys={state.targetKeys}
          disabled={state.disabled}
          showSearch={state.showSearch}
          onChange={onChange}
          filterOption={(inputValue, item) =>
            item.title.indexOf(inputValue) !== -1 || item.tag.indexOf(inputValue) !== -1
          }
          leftColumns={leftTableColumns}
          rightColumns={rightTableColumns}
        /> 
        <div className="parcelUpdate">
          <Link to="/Business/OrderHome">
            <Button size="large" style={{ float: 'center', margin: 5 }}>
              
            </Button>
          </Link>
          <Button id="submitOrder" size="large" style={{ float: 'center', margin: 5}} onClick={submitOrder}> 
            
          </Button> 
        </div>
      </div>
    );
}
