import React, { useState } from 'react';
 
import { Form, Row, Col, Input, Button,AutoComplete,notification } from 'antd';
//import DisplayTBForm from "./DisplayTBForm";
import { DownOutlined, UpOutlined } from '@ant-design/icons';
import "./TBForm.css";

function TBForm(props){ 

  // props.user.obj.username
  const [form] = Form.useForm(); 

  const openNotification = () => {
    notification['success']({
      message: 'Successfully Add New Parcel',
      description:
        'You have successfully add an new Parcel, you can not manipulate it until it become verify',
      })
  };


  const onFinish = values => {
    const postInfo = {
                        orderNumber:values.company+" "+values.courier, // 
                        senderName:values.senderName,
                        senderAddress:values.senderAddress,
                        senderPhone:values.senderPhone,
                        senderPostCode:values.senderPostCode,
                        userId:props.user.obj.uid,
                        receiverName:"",   //    null
                        receiverAddress:"",
                        receiverPhone:"",
                        receiverPostCode:"",
                        contentType:values.contentType,
                        description:values.description,
                        //  "userOrderId": -1  -1  4  -1 
                    }

    const returnInfo = JSON.parse(JSON.stringify(postInfo));
          returnInfo.userOrderId = -1;
          returnInfo.parcelStatus = "waiting";
          
       
    const newRecord = props.record.concat(returnInfo)  //
                  
    console.log("stringfy new TB record newnew:")
    console.log(JSON.stringify(postInfo))
 
    fetch("/api/parcel/create", { 
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(postInfo),
    })
      .then(
        openNotification(),
        form.resetFields()
        )  
      .then(
        console.log("newRecord in set Record"),
        console.log(newRecord),
        props.setRecord(newRecord)); ;  //     clear 
  };

  // onFinish 

  const renderTitle = title => (
    <span>
      {title}
    </span>
  );
  
  const renderItem = (title) => ({
    value: title,
    label: (
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        {title}
      </div>
    ),
  });

  const options_type = [
    {
      label: renderTitle(""),
      options: [renderItem(""),renderItem(""),renderItem(""),renderItem(""),renderItem(""),renderItem("")],
    }
  ];
  
  const options_company  =[{value:"SF"},{value:"EMS"},{value:"BS"},{value:"ZT"},{value:"ST"},{value:"YT"},{value:"YD"},{value:"TT"},{value:"ZJS"},{value:"DB"},]
  
    return (
      <Form
        form={form}
        name="InputTBCard"
        className="ant-advanced-search-form"
        onFinish={onFinish}
      >
        <Row gutter={24}>

        <Col span={6} key={1}>
            <Form.Item
              name="company"
              label=""
              rules={[
                {
                  required: true,
                  message: '',
                },
              ]}
            >
            <AutoComplete
              style={{
                //width: 200,
              }}
              options={options_company}
              placeholder=""
              filterOption={(inputValue, option) =>
                option.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
              }>
              <Input />  
              </AutoComplete>
            
            </Form.Item>
          </Col>

          <Col span={6} key={2}>
            
            <Form.Item
              name="courier"
              label=""
              rules={[
                {
                  required: true,
                  message: '',
                },
              ]}
            >
              <Input />
            </Form.Item>
           
          </Col>

          <Col span={6} key={3}>
            <Form.Item
              name="contentType"
              label=""
              rules={[
                {
                  required: true,
                  message: '',
                },
              ]}
            >
              <AutoComplete
                dropdownClassName="certain-category-search-dropdown"
                //dropdownMatchSelectWidth={200}
                style={{
                  //width: 250,
                }}
                options={options_type}
              >
                <Input.Search placeholder="" />   
              </AutoComplete>
            </Form.Item>
          </Col>
  
          <Col span={6} key={4}>
            <Form.Item
              name="description"
              label=""
              rules={[
                {
                  required: true,
                  message: '',
                },
              ]}
            >
              <Input placeholder="eg: " />
            </Form.Item>
          </Col>
        </Row>

        <Row gutter={24}>

          <Col span={6} key={5}>
              <Form.Item
                name="senderName"
                label="* "
                rules={[
                  {
                    required: false,
                    message: '',
                  },
                ]}
              >
                <Input placeholder=""/>
              </Form.Item>
            </Col>

            <Col span={6} key={6}>
              
              <Form.Item
                name="senderAddress"
                label="* "
                rules={[
                  {
                    required: false,
                    message: '',
                  },
                ]}
              >
                <Input placeholder="" />
              </Form.Item>
            
            </Col>

            <Col span={6} key={7}>
              <Form.Item
                name="senderPostCode"
                label="* "
                rules={[
                  {
                    required: false,
                    message: '',
                  },
                ]}
              >
                  <Input />   
             
              </Form.Item>
            </Col>

            <Col span={6} key={8}>
              <Form.Item
                name="senderPhone"
                label="* "
                rules={[
                  {
                    required: false,
                    message: '',
                  },
                ]}
              >
                <Input placeholder=""/>
              </Form.Item>
            </Col>
          </Row>

        <Row>
          <Col
            span={24}
            key={9}
            push={10}
            style={{
              textAlign: 'center',
            }}
          >
            <Button type="primary" htmlType="submit" >   
              Add
            </Button>
            <Button
              style={{
                margin: '0 8px',
              }}
              onClick={() => {
                form.resetFields();
              }}
            >
              Clear
            </Button>
          </Col>
        </Row>
      </Form>
    )}

  export default  TBForm;

  
