import React,{useState}from "react";
import _get from "lodash/get";
import {Table, Form, Popover,Button,Input,notification } from "antd";
import {EditOutlined} from '@ant-design/icons';
import "./HomePersonInfo.css";
 
const FormItem = Form.Item;

export default function HomePersonalInfo(props) {

  const email = props.record.map(item => {return item.email})  // 

  /* Pop  email */

  const [resetPopState, setResetPopState] = useState({visible:false,})

  const resetPassword =()=>{
    console.log("reset password")  // 
    setResetPopState({visible:false,})
}
  const handleVisibleChange = visible => {
    setResetPopState({ visible });
};

  /*   */
  
    const openNotification = () => {
        notification['success']({
          message: 'Successfully update user information',
          description:
            'You have successfully unpdate your personnal information',
          })
      };

     
    const FormEdit=()=>{
  
      const onFinish = values => {
        console.log('Received values of form: ', values); // update setState 
        
      const newUserInfo = {
          uid:props.user.obj.uid,  // id
          username:props.user.obj.username,// ID  
          phone:values.phone,
          email:values.email,
          role:props.user.obj.role,
          address: values.address
        };
  
      fetch("/api/user/update", { 
        method: "PUT",
        headers: {
        "Content-Type": "application/json"
        },
        body: JSON.stringify(newUserInfo),
        })
        .then(resp => resp.json())
        .then(data=> props.setRecord([data]))
        .then(openNotification()) //    clear  
      };
      return(
      <Form onFinish={onFinish}
      initialValues={{
      }}>
  
        <FormItem 
        name="email"
        rules={[
          {
            required: true,
            message: 'Please input your email!',
          },
        ]}>
            <Input placeholder="Email" />   
        </FormItem>
  
        <FormItem  
        name="phone"
        rules={[
          {
            required: true,
            message: 'Please input your phoneNumber!',
          },
        ]}>
            <Input placeholder="Phone" />    
        </FormItem>
  
        <FormItem 
        name="address"
        rules={[
          {
            required: false,
          },
        ]}>
          <Input placeholder="Add Address" />
        </FormItem>
  
        <FormItem>
          <Button type="primary" htmlType="submit">  
            Submit
          </Button> 
        </FormItem>
      </Form>
    );}
  
    const columns = [
      {
        title: "",
        dataIndex: "username",
        key: "name"
      },
      {
        title: "",
        dataIndex: "email",
        key: "email"
      },
      {
        title: "",
        dataIndex: "phone",
        key: "phone"
      },
      {
        title: "",
        dataIndex: "fakePassword",
        key: "fakePassword"
      },
      {
        title: "",
        dataIndex: "address",
        key: "address"
      },
      {
        title: "",
        dataIndex: "action",
        key: "action",
        render: (text, record, index) => {
          return (
            <div>
              <Popover
                content={FormEdit()}   
                title={props.user.obj.username + " edit"}  //{props.user.obj.username + " edit"} //  props.user.obj   const objectId = _get(item, "id");
                trigger="click"
                placement="left"
              >
                <Button icon={<EditOutlined />} />
              </Popover>
            </div>
          );
        }
      },
  
      {
        title: "",
        dataIndex: "action",
        key: "action",
        render: (text, record, index) => {
          return (
            <div>
              <Popover
                content={
                <div>
                <p>  {email} </p>
                <p>  </p>
                <a onClick={resetPassword}></a>
                </div>} 
                title={""+ props.user.obj.username + "" }  //{props.user.obj.username + " edit"} //  props.user.obj   const objectId = _get(item, "id");
                trigger="click"
                placement="left"
                visible={resetPopState.visible}
                onVisibleChange={handleVisibleChange}
              >
                <Button icon={<EditOutlined />} />
              </Popover>
            </div>
          );
        }
      }
      
    ];

  let tableDataSource;

  if(props.record){
    tableDataSource = props.record.map(item => {
      return {
        ...item,
        key: item.uid
      }
    })
  }
  
    return (
      <div>
        <p>Hello Dear you already login below is your information</p>
        <br/>
        <Table dataSource={tableDataSource} columns={columns} />
      </div>
    );


}
