import React from "react";
import "./CreateOrder.css";
import { Form, Row, Col, Input, Button,AutoComplete,notification } from 'antd';
 
import { DownOutlined, UpOutlined } from '@ant-design/icons';
import "./CreateOrder.css";

 //const ReturnInfo = JSON.parse(JSON.stringify(postInfo)) obj

export default function CreateOrder(props){ 

  const [form] = Form.useForm(); 

  // 

  const openNotification = () => {
    notification['success']({
      message: 'Successfully Add New Order',
      description:
        'You have successfully add an new order, please add parcel you wanna put into it in the table below',
      })
  };

  // onFinish  homeRecords => dataSource=> orderRecord 
  // home datasource create 
    
  const onFinish = values => {
    const postInfo = {userId:props.user.obj.uid,  // id  
                      statusId: 1,   // 1 = New
                      price: null,
                      description: values.extraInfo,
                      receiverName:values.receiverName,
                      receiverPhone:values.receiverPhone,
                      receiverAddress:values.country +"/"+ values.province +"/"+ values.address01+"/"+values.address02,
                      receiverPostCode:values.receiverPostCode,
                      orderId: null,
                      trackNumber: null,
                      senderName: null,
                      senderAddress: null,
                      senderPhone: null,
                      senderPostCode: null,
                    };
    
    fetch("/api/order/create", { 
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(postInfo),
    })
      .then(resp => resp.json())
      .then(data => {
        data.parcels = [];   //  record
        let newRecord = [];
        newRecord = props.record.concat(data)   // order 
        return newRecord
      })
      .then(resp=>{
        console.log("new records entered?")
        console.log(resp)
        props.setRecord(resp);  // form.resetFields()  clear
      })
      .then(openNotification())  //  
      .catch(error=>{})   // catch error   
  };

  const renderTitle = title => (
    <span>
      {title}
    </span>
  );
  
  const renderItem = (title) => ({
    value: title,
    label: (
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        {title}
      </div>
    ),
  });


  /*    autocomplete */

const country = ["","","",""] //     
const province = ["ontario","Quebec","Toronto","vancouver"]
const Opt_country = country.map(function(item,index){return {value:item}})
const Opt_province = province.map(function(item,index){return {value:item}})

  
  
    return (
      <Form
        form={form}
        name="InputTBCard"
        className="ant-advanced-search-form"
        onFinish={onFinish}
      >
        <Row gutter={24}>

        <Col span={6} key={1}>
            <Form.Item
              name="country"
              label=""
              rules={[
                {
                  required: true,
                  message: '',
                },
              ]}
            >
            <AutoComplete
              style={{
                //width: 200,
              }}
              backfill="true"
              options={Opt_country}
              filterOption={(inputValue, option) =>
                option.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
              }>
              <Input />  
              </AutoComplete>
            
            </Form.Item>
          </Col>

          <Col span={6} key={2}>
            
            <Form.Item
              name="province"
              label=""
              rules={[
                {
                  required: true,
                  message: '',
                },
              ]}
            >
              <AutoComplete
              style={{
                //width: 200,
              }}
              options={Opt_province}
              filterOption={(inputValue, option) =>
                option.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
              }>
              <Input />  
              </AutoComplete>
            
            </Form.Item>
           
          </Col>

          <Col span={6} key={3}>
            <Form.Item
              name="address01"
              label=""
              rules={[
                {
                  required: true,
                  message: '',
                },
              ]}
            >
              
                <Input placeholder="eg: Apt ###, ** rue/avenue" />   
             
            </Form.Item>
          </Col>
          
          <Col span={6} key={4}>
              
              <Form.Item
                name="address02"
                label="* "
                rules={[
                  {
                    required: false,
                  },
                ]}
              >
                <Input />
              </Form.Item>
            
            </Col>

           
            <Col span={6} key={5}>
              <Form.Item
                name="receiverPostCode"
                label=""
                rules={[
                  {
                    required: true,
                    message: '',
                  },
                ]}
              >
                  <Input />   
             
              </Form.Item>
            </Col>
            

          <Col span={6} key={6}>
              <Form.Item
                name="receiverName"
                label=""
                rules={[
                  {
                    required: true,
                    message: '',
                  },
                ]}
              >
                <Input />
              </Form.Item>
            </Col>

            <Col span={6} key={7}>
              <Form.Item
                name="receiverPhone"
                label=""
                rules={[
                  {
                    required: true,
                    message: '',
                  },
                ]}
              >
                <Input placeholder=""/>
              </Form.Item>
            </Col>

            <Col span={6} key={8}>
            <Form.Item
              name="extraInfo"
              label="* "
              rules={[
                {
                  required: false,
                  message: '***',
                },
              ]}
            >
              <Input />
            </Form.Item>
          </Col>

          </Row>

        <Row>
          <Col
            span={24}
            key={9}
            push={10}
            style={{
              textAlign: 'center',
            }}
          >
            <Button type="primary" htmlType="submit" >   
              Add
            </Button>
            <Button
              style={{
                margin: '0 8px',
              }}
              onClick={() => {
                form.resetFields();
              }}
            >
              Clear
            </Button>
          </Col>
        </Row>
      </Form>
    )}
  

  //<Cascader options={options} onChange={onChange} /> cascader 

  //const ReturnInfo = JSON.parse(JSON.stringify(postInfo)) obj