
import React, { useContext } from "react";
import { Route } from "react-router-dom";

import { Layout, Menu, Breadcrumb,Row, Col } from 'antd';

import UserContext from '../Context/UserContext';
import TopBar from "../TopBar/TopBar";
import Business from "../BusinessSum/Business/Business";   //  /Views/Business/Business
import Contact from "../Contact/Contact";
import Price from "../Price/Price";
import HDashBoard from "../HelpCenter/HDashBoard/HDashBoard";
import Register from "../Register/Register";
import SignIn from '../SignIn/SignIn';
import Home from '../HomeSum/Home/Home';
import Bottom from '../Bottom/Bottom';
import ForgetPassword from '../SignIn/ForgetPassword';
import AdminDashBoard from '../HomeSum/HomeAdmin/AdminDashBoard/AdminDashBoard';
import "./DashBoard.css";
//import UserInfo from "../Business/UserInfo/UserInfo";


const { Header, Content, Footer } = Layout;

export default function Dashboard() {

    const user = useContext(UserContext)[0]; //  user hook {}
  
    // console.log("usecontext:user value")
   console.log(user)   // {}   OBJ
   // console.log(user.obj)
    //console.log(user.obj.username)

  return (
    <Layout>
      <Header>
      <TopBar user={user} />
      </Header>

      <Content style={{ padding: '0px 50px' }}>
      
      <Route
        exact path="/"
        render={internalProps => (
          <Home
            {...internalProps}
            user={user}
            isAuthed={true} 
          />
        )}
      />
        <Route
        path="/Admin"
        render={internalProps => (
          <AdminDashBoard
            {...internalProps}
            user={user}
            isAuthed={true} 
          />
        )}
      />
      <Route
        path="/Business"
        render={internalProps => (
          <Business
            {...internalProps}
            user={user}
            isAuthed={true} 
          />
        )}
      />
      <Route path="/HelpCenter" component={HDashBoard} />

      <Route exact path="/Contact" component={Contact} />
      <Route exact path="/Price" component={Price} />
      <Route exact path="/Register" component={Register} /> 
      <Route exact path="/SignIn" component={SignIn}/> 
      <Route exact path="/SignIn/ForgetPassword" component={ForgetPassword}/> 
     
    </Content>
    
    <Footer style={{ textAlign: 'center' }}>
      <Bottom/>
    </Footer>
    </Layout>
  );
}

