import React from "react";
import _get from "lodash/get";
import {Table, Button,Tag,notification } from "antd";
import {EditOutlined,StopOutlined,PlusSquareOutlined,VerticalAlignTopOutlined} from '@ant-design/icons';
import { Link } from "react-router-dom";
 
import "./OrderRecord.css";

// antd table columns button 
//https://stackoverflow.com/questions/52767825/unable-to-update-the-state-when-button-inside-table-cell-clicked-react-ant-des

//   2   setState 


export default function OrderRecord(props) {

// props.setRecord  

  // const userId = props.user.obj.uid;
 

  /***   ** */
    const openNotification = (prop) => {
      notification['success']({
        message: '',
        description:
          ' ' + prop.id + '  '
        })
    };
  
    //  01request
    
  const SubmitOrder = (record)=>{

   fetch(`/api/order/submit/user/${props.user.obj.uid}/userorder/${record.id}`, { 
    method: "PUT",
    headers: {
    "Content-Type": "application/json"
    },
    })
    .then(resp => resp.json())
    .then(resp => openNotification(resp))
    .then(props.setRecord([]))      // record record use Effect record.length  
  }

  const FinishOrder = (record)=>{
    fetch(`/api/order/finish/user/${props.user.obj.uid}/userorder/${record.id}`, { 
      method: "PUT",
      headers: {
      "Content-Type": "application/json"
      },
      })
      .then(resp => resp.json())
      .then(resp => openNotification(resp))
      .then(props.setRecord([])) 
  }

  const columns = (SubmitOrder,FinishOrder)=>{
    return [
    {
      title: "",
      dataIndex: "id",
      key: "id",
      render: text => <div>{text}</div>,
    },
    
    {
      title: "",
      dataIndex: "parcelString",
      key: "parcelString",
      render: text => <div>{text}</div>,
    },
   

    {
      title: '',
      key: 'statusId',
      dataIndex: 'statusId',
      render: (text, record, index) => { 
            let status = ""
            let color = ""  
            if (record.statusId === 1){
              color = "defaut";
              status = ""
            } else if(record.statusId === 2){
              color = "success";
              status = ""
            } else if(record.statusId === 3){
              color = 'error';
              status = ""
            }
             else if(record.statusId === 4){
              color = "warning";
              status = ""
            }
            else if(record.statusId === 5){
              color = "processing";
              status = ""
            }
            else if(record.statusId === 6){
              color = "defaut";
              status = ""
            }
            return (
              <Tag color={color} key={index}>
                {status}
              </Tag>
            );
          }},
  

    {
      title: "",  //   record.id 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => {  // recorddata  datasource  
        if(record.statusId === 1){return (
          <div>
              <Link to={`/Business/OrderHome/AddParcel/${record.id}`} className="LearnMore">
              <PlusSquareOutlined/>
              </Link>
          </div>
        );} else{return(
          <div>
                <StopOutlined />   
          </div>
        )}
      }
    },

    {
      title: "",  //   record.id 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => {  // recorddata  datasource   record = props.dataSource
        if(record.statusId === 1){return (
          <div>
              <Link to={`/Business/OrderHome/OrderInfo/${record.id}`} className="More">
              <EditOutlined/>
              </Link>
          </div>
        );}
        else{return(
          <div>
                <StopOutlined />   
          </div>
        )}
      }
    },

    {
      title: "", 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => { 
        if(record.statusId === 1){return (
          <Button shape="round" type="primary" icon ={<VerticalAlignTopOutlined />} onClick={()=>SubmitOrder(record)}>     
          </Button>
        );}
        else{return(
          <Button shape="round" icon ={<VerticalAlignTopOutlined />} disabled = {true} />     
        )}
      }
    },

    {
      title: "",  //   record.id 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => {  // recorddata  datasource   record = props.dataSource 
        if(record.statusId === 1 || record.statusId === 2 || record.statusId === 3){return (   
              <Button shape="round" type="primary" disabled = {true} >     
              
              </Button>  
        );}
        else if(record.statusId === 4){return (
              <Link to={`/Business/OrderHome/PriceInfo/${record.id}`} className="MorePrice">
              <Button shape="round" type="primary" >     
              
              </Button>
              </Link>
        );}
        else {return (
          <div>
              <Link to={`/Business/OrderHome/PriceInfo/${record.id}`} className="MorePrice">
              <Button shape="round" type="default" >     
              
              </Button>
              </Link>
          </div>
        );}
      }
    },

    {
      title: "", 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => {  
        if(record.trackNumber === null){
          return(<div></div>)
        }
        else{
          return(
          <Link to={`/Business/OrderHome/TackingInfo/${record.id}`} className="MoreTrack">
          <p>{record.trackNumber}</p>
          </Link>
          ) 
        }       
      }
    },   
    {
      title: "", 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => { 
        if(record.statusId === 5){return (
          <Button shape="round" type="primary" onClick={()=>FinishOrder(record)}>  
               
          </Button>
        );}
        else if(record.statusId === 6){
          return(
            <span></span>   
          )
        }
        else{return(
          <Button shape="round" type="primary" disabled = {true} >    
             
          </Button>
        )}
      }
    }, 
  ];}

  // 

  var historyData = props.dataSource.filter(function(rec){
    if(rec){return rec.statusId !== 6}
    else{return([])}
  })

  let tableDataSource;
  if(historyData){
    tableDataSource = historyData.map(item => {
      return {
        ...item,
        key: item.id
      }
    })
  }
  return (
    <div>
      <p>Hello Dear Below is your order record</p>
      <br/>
      <Table 
      dataSource={tableDataSource}
      //columns={columns}
      columns={columns(SubmitOrder,FinishOrder)}   // 
      expandable={
        {
          expandedRowRender: (record) => {
            return(
            <div>
              <p> &nbsp; {record.receiverName} &nbsp;  {record.receiverPhone}</p>
              <p> {record.receiverAddress } &nbsp; {record.receiverPostCode}</p>
              <p>{record.description}</p>
            </div>
            )}
        }
      }
      
       />
      
    </div>
  );
}
