import React,{useState,useEffect}from 'react';
import 'antd/dist/antd.css';
import AParcelSearch from "../AParcelSearch/AParcelSearch";
import AParcelForm from "../AParcelForm/AParcelForm";
import "./AdminParcel.css"

export default function AdminParcel(props){

  console.log("AdminParcel")
  // console.log(props.user.obj.uid)   // number

  //const userId = props.user.obj.uid.toString();

  const [search,setSearch] = useState({searchRg:"",searchTy:"",userId:"",username:"",orderId:""}) ;        // userId is the only way to get orderInfo

  const [record,setRecord] = useState([]);

  console.log("search")
  console.log(search)

  console.log(record)
  
  useEffect(()=>{
    if(search.userId){
      fetch(`/api/parcel/findAll/userid/${search.userId}`, { 
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      })
      .then(resp => resp.json())
      .then(data => {
        setRecord(data);  
       })     
  }},[search.userId]) ;

  useEffect(()=>{
    if(search.username){
      fetch(`/api/parcel/findAll/username/${search.username}`, { 
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      })
      .then(resp => resp.json())
      .then(data => {
        setRecord(data);  
       })     
  }
  },[search.username]) ;

  useEffect(()=>{
    if(search.orderId){  //  orderID
      fetch(`/api/parcel/findAll/${search.orderId}`, { 
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      })
      .then(resp => resp.json())
      .then(data => {
        setRecord(data);  
      })  
    }},[search.orderId]);  //  5-13-14   15-15  setSearch  use Effect recordreset

    useEffect(()=>{
      if(search.searchRg === "searchAll"){  //  orderID
        fetch(`/api/parcel`, { 
          method: "GET",
          headers: {
            "Content-Type": "application/json"
          },
        })
        .then(resp => resp.json())
        .then(data => {
          setRecord(data);  
        })  
      }},[search.searchRg]) 

      useEffect(()=>{
        console.log(`/api/parcel/status/${search.searchTy}`)
        if(search.searchTy){  //  orderID
          fetch(`/api/parcel/status/${search.searchTy}`, { 
            method: "GET",
            headers: {
              "Content-Type": "application/json"
            },
          })
          .then(resp => resp.json())
          .then(data => {
            setRecord(data);  
            
          })  
        }},[search.searchTy]) 
  
  return (
    <div className="OrderHome">
    
        <p> OrderHome </p>  
        <div className="AParcelSearch">
          <AParcelSearch user={props.user} search={search} setSearch={setSearch}/>
        </div>
        <div>
          <AParcelForm  user={props.user} record={record} setRecord={setRecord} />
        </div>
        
    </div>
  ) 
  
}
