import React,{useState, useEffect}from "react";
import {Button,notification } from 'antd';
import { Link } from 'react-router-dom';
import _get from "lodash/get";
 
import "./TrackingHome.css";


export default function TackingHome(props) {

  const [dataSource,setDataSource] = useState([])    //   

  // const [originTargetKeys, setOriginTargetKeys] = useState([]) // 

  const userId = props.user.obj.uid.toString();

  const thisOrderId = props.match.params.id; 

  useEffect(()=>{
    fetch("/api/order/findAll/"+ userId, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   
    .then(data => {
        setDataSource(data)
     })
  },[])

  const thisOrderData = dataSource.filter(item=>item.id == thisOrderId) // order
  const trackNumber = _get(thisOrderData[0],"trackNumber") 
  const expectDeliveryDate = _get(thisOrderData[0],"expectDeliveryDate")

  const TrackOrder = ()=>{console.log("TrackOrder")}
 
    return (
      <div>
        <p>  {trackNumber}   {expectDeliveryDate} </p>

        <p>  </p>
        <Button id="submitOrder" size="large" style={{ float: 'center', margin: 5}} onClick={TrackOrder}> 
            
        </Button>

        <Link to="/Business/OrderHome">
        <Button size="large" style={{ float: 'center', margin: 5 }}>
            
        </Button>
        </Link>        
      </div>
    );
}
